import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';

import otherQuestionService from '../../_services/other-question.service';

function ResultRow(props) {


  const resultLink = `/response/${props.link}/${props.userId}`

  const key = props.index;
  return (
    <tr key={key}>
      <td> {key}</td>
      <th scope="row"> <Link to={resultLink}> {props.userId}</Link></th>
      <td> {props.score}/{props.number_of_questions} </td>

    </tr>
  )

}

class UserResults extends Component {
  constructor(props) {
    super(props);
    this.state = {
      results: [],
      quizLink:''

    }
  }
  async getResults() {
    let link = this.props.match.params.quizLink;
    await otherQuestionService.getResultsByQuizLink(link).then(res => {

        this.setState({
          results: res.data,
          quizLink:link

        })


    }).catch(err => (
      console.log(err) ));
  }
  async componentDidMount() {
    await this.getResults();
  }
  render() {

    const { results, quizLink } = this.state;
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xl={12}>
            <Card>
              <CardHeader>
                <i className="fa fa-align-justify"></i> Your Responses
              </CardHeader>
              <CardBody>
              <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">id</th>

                      <th scope="col">name</th>
                      <th scope="col"> score</th>


                    </tr>
                  </thead>
                  <tbody>
                    {results.map((result, index) =>
                      <ResultRow key={index} index={index + 1} link={quizLink} {...result} />
                    )}
                  </tbody>
                </Table>

                </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default UserResults;
